import React, { useState, useEffect } from 'react';
import { joinRoom, Room } from 'trystero/torrent';

const config = { appId: 'text-sync-demo' };

export default function TextSyncDemo() {
  const [text, setText] = useState('');
  const [peers, setPeers] = useState<string[]>([]);
  const [room] = useState<Room>(() => joinRoom(config, 'text-room'));
  const [sendText, getText] = room.makeAction<string>('text');

  useEffect(() => {
    // Listen for text updates from other peers
    getText((newText, peerId) => {
      console.log(`Received text from peer ${peerId}`);
      setText(newText);
    });

    // Track peer joins
    room.onPeerJoin(peerId => {
      console.log(`Peer ${peerId} joined`);
      setPeers(prev => [...prev, peerId]);
      // Send current text to new peer
      sendText(text, peerId);
    });

    // Track peer leaves
    room.onPeerLeave(peerId => {
      console.log(`Peer ${peerId} left`);
      setPeers(prev => prev.filter(id => id !== peerId));
    });

    // Cleanup when component unmounts
    return () => room.leave();
  }, [room, sendText, getText, text]);

  const handleTextChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const newText = e.target.value;
    setText(newText);
    // Broadcast text to all peers
    sendText(newText);
  };

  return (
    <div className="p-4 max-w-2xl mx-auto">
      <div className="mb-4 space-y-2">
        <h1 className="text-2xl font-bold">Trystero Text Sync Demo</h1>
        <p className="text-gray-600">
          {peers.length === 0 
            ? "No other peers connected" 
            : `${peers.length} peer${peers.length === 1 ? '' : 's'} connected`}
        </p>
      </div>
      
      <div className="space-y-2">
        <label htmlFor="syncedText" className="block text-sm font-medium">
          Shared Text (synced between all peers):
        </label>
        <textarea
          id="syncedText"
          value={text}
          onChange={handleTextChange}
          className="w-full h-40 p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
          placeholder="Type something here..."
        />
      </div>
    </div>
  );
}